<template>
	<div id="takeout-log">
		<public-header title="订单日志"></public-header>
		<div class="content">
			<van-steps direction="vertical" :active="logs.length - 1" active-color="#4FAE52">
				<van-step class="border-0px" v-for="(item, index) in logs" :key='index'>
					<h3>【{{item.title}}】操作人: {{item.role_cn}}</h3>
					<p>{{item.addtime_cn}}</p>
				</van-step>
			</van-steps>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	export default {
		data() {
			return {
				id: 0,
				logs: [],
				showPreLoading: true,
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onLoad() {
				this.util.request({
					url: 'plateform/order/takeout/logs',
					data: {
						id: this.id
					}
				}).then((res) => {
					this.showPreLoading = false;
					let result = res.data.message;
					if(result.errno) {
						this.$toast(result.message)
						return;
					}
					this.logs = result.message.logs;
				})
			}
		},
		created() {
			if(this.$route.query && this.$route.query.id) {
				this.id = this.$route.query.id;
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style scoped>

</style>


